html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  color: #010101;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.imagen-small {
  width: 20rem;
}

.ocultar {
  display: none;
}

h1, h2, h3 {
  font-family: "Montserrat", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #fff;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.todo {
  background-color: #197fd5;
  border-radius: 1rem;
  padding: 1rem;
}
.todo .header {
  display: block;
  padding: 2.5rem 2rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .todo .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 95%;
  }
}
.todo .header .logo picture img {
  max-width: 25rem;
}
.todo .header .iconos {
  padding: 1.5rem;
  background-color: #fff;
  border-radius: 25rem;
}
.todo .header .iconos a {
  color: #010101;
}

.cuerpo {
  display: block;
  color: #fff;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .cuerpo {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 2rem;
    height: auto;
  }
}
.cuerpo .navegacion {
  text-align: center;
}
@media (min-width: 1024px) {
  .cuerpo .navegacion {
    text-align: left;
  }
}
.cuerpo .navegacion li {
  list-style: none;
  margin-bottom: 3rem;
}
.cuerpo .navegacion li a {
  display: flex;
  align-items: center;
  color: #fff;
}
.cuerpo .navegacion li a p {
  margin-left: 1rem;
}
.cuerpo .contenido {
  background-color: #fff;
  border-radius: 1rem;
  color: #197fd5;
  padding: 1rem 0;
}

.tarjeta {
  background-color: #fff;
  padding: 2rem 0;
  margin: auto;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .tarjeta {
    max-width: 50%;
  }
}

.perfil {
  text-align: center;
}
.perfil a {
  color: #fff;
  font-weight: 700;
}

.detalles {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.acciones {
  display: block;
}
@media (min-width: 1024px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.totales .total {
  text-align: right;
  color: #010101;
}
.totales .total p {
  color: #010101;
}

.usuarios {
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.usuarios .usuario {
  background-color: #228ce5;
  border-radius: 1rem;
  margin: 1rem;
  padding: 1rem;
}
.usuarios .usuario p {
  text-align: center;
}
.usuarios .usuario .nombre-usuario {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 2.5rem;
}
.usuarios .usuario .email-usuario {
  font-size: 1.5rem;
}

.secciones {
  overflow: scroll;
}
.secciones .listado-productos {
  display: block;
  margin: 1rem;
}
@media (min-width: 768px) {
  .secciones .listado-productos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.secciones .listado-productos .producto {
  border-radius: 1rem;
  margin: 1rem;
}
.secciones .listado-productos .producto h3 {
  text-transform: capitalize;
  color: #686868;
}
.secciones .listado-productos .producto p {
  text-align: center;
  color: #1c1c1c;
}
.secciones .listado-productos .producto .imagen-producto {
  margin: 1rem auto;
  background-color: #fff;
  padding: 2rem;
  width: 60%;
  border-radius: 50%;
}
.secciones .listado-productos .producto .imagen-producto img {
  width: 100%;
  border-radius: 50%;
}

.seccion {
  display: none;
  padding: 5rem 0;
}

.nuevo_cliente {
  width: 50%;
}
.nuevo_cliente a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #197fd5;
}
.nuevo_cliente a p {
  color: #197fd5;
  margin-left: 1rem;
  font-weight: 700;
}
.nuevo_cliente a:hover {
  color: #0e497a;
}

@media (min-width: 768px) {
  .todoPaso2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
.todoPaso2 .derechaPaso2 table {
  width: 95%;
  margin: 0 auto;
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.producto {
  margin: 0 2rem;
}
.producto .cod-producto {
  color: #353535;
  text-align: right;
  margin: 0;
  padding: 0;
}
.producto .cantidades {
  display: flex;
  justify-content: space-evenly;
}
.producto p {
  margin: 0;
  text-align: center;
  color: #353535;
  font-size: 10px;
}
.producto h3 {
  text-transform: capitalize;
  font-size: 12px;
}
.producto h4 {
  text-align: center;
  font-size: 25px;
}

.formulario {
  margin: 5rem 1rem;
  width: 100%;
}

.campo {
  margin-bottom: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .campo {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
}
.campo label {
  flex: 0 0 10rem;
}
.campo input,
.campo select,
.campo textarea {
  border: none;
  border-radius: 1rem;
  width: 90%;
  padding: 1.25rem;
  background-color: #f2f2f2;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .campo input,
.campo select,
.campo textarea {
    flex: 0.8333333333;
  }
}

.boton {
  background-color: #197fd5;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    max-width: 95%;
    margin: 0 auto;
  }
}
.boton:hover {
  background-color: #1464a7;
  cursor: pointer;
}

.boton-crear {
  background-color: #e3d617;
  padding: 1.5rem 4rem;
  color: #010101;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton-crear {
    max-width: 95%;
    margin: 0 auto;
  }
}
.boton-crear:hover {
  background-color: #b5aa12;
  cursor: pointer;
}

.botones-superior {
  display: block;
}
@media (min-width: 768px) {
  .botones-superior {
    display: flex;
  }
  .botones-superior a {
    width: 50%;
  }
}

.boton-borrar {
  background-color: #b81515;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton-borrar {
    max-width: 95%;
    margin: 0 auto;
  }
}
.boton-borrar:hover {
  background-color: #8a1010;
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton-borrar {
    text-align: center;
  }
}

.alerta {
  padding: 1.25rem;
  color: #fff;
  width: 95%;
  display: block;
  margin: 0 auto 2rem auto;
  border-radius: 1rem;
  text-align: center;
  font-weight: 700;
}
.alerta.error {
  background-color: #b81515;
}
.alerta.exito {
  background-color: #197fd5;
}

.buscador {
  display: flex;
  margin-top: 2rem;
  justify-content: space-evenly;
}
.buscador input[type=search] {
  border: 0.5px solid #7ebcf0;
  width: 40%;
  text-align: center;
  border-radius: 1rem;
}
.buscador .boton-crear {
  margin: 0;
  color: #fff;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
  .tabs button {
    background-color: #197fd5;
    border: none;
    color: #fff;
    padding: 1rem;
    display: block;
    width: 100%;
    margin-bottom: 1rem;
    text-transform: uppercase;
    font-weight: 700;
    transition: background-color 300ms ease;
  }
  .tabs button:hover {
    cursor: pointer;
    background-color: #1464a7;
  }
  .tabs button.actual {
    background-color: #fff;
    color: #197fd5;
  }
}

.paginacion {
  padding: 5rem;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}

.tabla-productos {
  width: 95%;
  margin: 0 auto;
}

.contenedor-nueva-garantia {
  display: flex;
  justify-content: center;
}
.contenedor-nueva-garantia .agregar-garantia {
  background-color: #e3d617;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .contenedor-nueva-garantia .agregar-garantia {
    max-width: 95%;
    margin: 0 auto;
  }
}
.contenedor-nueva-garantia .agregar-garantia:hover {
  background-color: #b5aa12;
  cursor: pointer;
}

.listado-garantia {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .listado-garantia {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
.listado-garantia .garantiaDIV {
  margin: 1rem;
  background-color: #197fd5;
  border-radius: 0.5rem;
}
.listado-garantia .garantiaDIV p {
  color: #fff;
  padding: 1rem;
  font-size: 1.5rem;
}
.listado-garantia .garantiaDIV .seriales {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}

.opciones {
  display: block;
}
@media (min-width: 1024px) {
  .opciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.opciones .estado-garantia:hover,
.opciones .aprobado:hover,
.opciones .rechazo:hover {
  cursor: pointer;
}

.pendiente {
  display: block;
  margin: 1rem auto;
  background-color: #e3d617;
  border: none;
  border-radius: 0.5rem;
  padding: 2.5rem;
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
  transition-property: background-color;
  transition-duration: 300ms;
}
.pendiente:hover {
  background-color: #b5aa12;
}

.aprobado {
  display: block;
  margin: 1rem auto;
  background-color: green;
  border: none;
  border-radius: 0.5rem;
  padding: 2.5rem;
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
  transition-property: background-color;
  transition-duration: 300ms;
}
.aprobado:hover {
  background-color: #004d00;
}

.rechazo {
  display: block;
  margin: 1rem auto;
  background-color: #b81515;
  border: none;
  border-radius: 0.5rem;
  padding: 2.5rem;
  color: #fff;
  font-size: 1.5rem;
  font-weight: bold;
  transition-property: background-color;
  transition-duration: 300ms;
}
.rechazo:hover {
  background-color: #8a1010;
}

.no-garantias a {
  color: #197fd5;
}

.nueva-garantia {
  background-color: #e3d617;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .nueva-garantia {
    max-width: 95%;
    margin: 0 auto;
  }
}
.nueva-garantia:hover {
  background-color: #b5aa12;
  cursor: pointer;
}

.listado-garantias {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-garantias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-garantias {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-garantias .garantia {
  background-color: #228ce5;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 0.5rem;
  margin: 0 1rem;
  transition-property: background-color;
  transition-duration: 300ms;
}
.listado-garantias .garantia:hover {
  transition-property: background-color;
  transition-duration: 300ms;
  background-color: #0e497a;
}
.listado-garantias .garantia a {
  color: #fff;
  text-transform: capitalize;
  font-size: 2rem;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #fff;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 300ms;
