@use 'base/variables' as v;
@use 'base/mixins' as m;

.cotizador {
    margin: 1rem;
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
    }

    .productoDiv{
        background-color: v.$azul;
        border-radius: .5rem;
        padding: 1rem;

        h3 {
            color: v.$blanco;
            font-size: 1.5rem;
            text-transform: capitalize;
        }

        h4 {
            text-align: center;
            color: v.$blanco;
            font-size: 1.8rem;
            font-weight: bold;
        }

        .cantidad-descuento {
            display: flex;
            justify-content: space-evenly;
            align-items: center;

            p {
                font-size: 1.25rem;
            }
        }


    }
}